* {
    padding: 0;
    margin: 0;
}
a {
    color: rgb(255, 255, 255);
    text-decoration: none;
}
.PageBody {
    // background: url(../Assets/images/cityBackground.png) center center no-repeat;
    color: #fff;
    background-color: black;
    background-size: cover;
    width: 100%;
    font-family: font1;
}

.header {
    padding: 1rem 3rem;
    nav {
        display: inline-flex;
        width: 95%;
        ul {
            display: inline-block;
            margin: auto;
            li {
                display: inline-block;
                list-style: none;
                padding: 0 1rem;
                text-align: center;
            }
        }
    }
}

.logo {
    display: inline-block;
    text-align: center;
    width: 5%;
}

.text {
    // font-family: 'Yellowtail', cursive;
    font-family: sans-serif;
    color: rgba(255, 255, 255, 0.1);
    text-align: center;
    padding-top: 1rem;
    height: fit-content;
    background: linear-gradient(
        to right,
        rgb(189, 76, 217),
        rgb(220, 52, 220),
        rgb(88, 86, 217),
        rgb(144, 52, 220),
        rgb(165, 76, 217)
    );
    background-size: 400%;
    -webkit-background-clip: text;
    font-weight: 700;
    animation: sTransition 10s linear infinite;
    z-index: 99;
    width: auto;
    display: inline-block;
    padding-right:10px ;
    font-size: 2rem;
}
@keyframes sTransition {
    0% {
        background-position: 0%;
    }
    100% {
        background-position: 400%;
    }
}



.TextBanner{
    z-index: 99;
}
#text , #secend {
    margin: 0 auto;
    margin-top: 1rem;
    font-size: 2rem;
    color: white;
    text-align: center;
    z-index: 99;
    height: fit-content;
    width: 100%;
}

.Banner {
    height: 94vh;
    display: flex;
    flex-direction: column;
    position: relative;
    z-index: 1;
    .ButtonIMG {
        width: 100%;
        height: 100%;
        z-index: 1;
        position: absolute;
        bottom: 0;
        left: 0;
        background: url(../Assets/images/macnook-light.jpg) center center no-repeat;
        background-size: cover;
        &::after {
            content: "";
            width: 100%;
            position: absolute;
            height: 100%;
            display: inline-block;
            top: 0;
            
        }
    }
}

.Profile {
    padding-top: 2rem;
    max-width: 80rem;
    margin: auto;
    height: 100vh;
    .Title{

        img{
            width: 18rem;
            height: 18rem;
            border-radius: 100%;
            object-fit: cover;
        }
    }
}

@import url('https://fonts.googleapis.com/css?family=Montserrat');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: 'Montserrat', sans-serif;
  background-color: #000;
  color: #eee;
}
.container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 99;
}
.container p {
  font-size: 3rem;
  padding: 0.5rem;
  font-weight: bold;
  letter-spacing: 0.1rem;
  text-align: center;
  z-index: 99;
  overflow: hidden;
}
.container p span.typed-text {
  font-weight: normal;
  color: #dd7732;  z-index: 99;

}
.container p span.cursor {
  display: inline-block;
  background-color: #ccc;  z-index: 99;

  margin-left: 0.1rem;
  width: 3px;
  animation: blink 1s infinite;  z-index: 99;

}
.container p span.cursor.typing {
  animation: none;  z-index: 99;

}
@keyframes blink {
  0%  { background-color: #ccc; }
  49% { background-color: #ccc; }
  50% { background-color: transparent; }
  99% { background-color: transparent; }
  100%  { background-color: #ccc; }
}